body {
  font-family: $base-font, $fallback-font;
  font-size: $base-font-size;
}
::selection {
  background-color: $sha-font-tangarine;
  color: $sha-font-white;
}
.h1,
.h2,
.h3,
.h4,
.h5,
.h6,
h1,
h2,
h3,
h4,
h5,
h6 {
  @include headings;
}
.h1,
h1 {
  font-size: 2.5rem;
}
.h2,
h2 {
  font-size: 2rem;
}
.h3,
h3 {
  font-size: 1.750rem;
}
.h4,
h4 {
  font-size: 1.5rem;
}
.h5,
h5 {
  font-size: 1.25rem;
}
.h6,
h6 {
  font-size: 1rem;
}

a {
  @include link;
}

p {
  font-size: inherit;
  line-height: $base-line-height;
  margin-bottom: 1rem;
}

.content-padding {
  padding-top: 6rem;
  padding-bottom: 6rem;
}
.text-align-center {
  text-align: center;
}

.bold {
  font-family: $bold-font;
}
.light {
  font-family: $light-font;
}
.normal {
  font-family: $base-font;
}